import React from 'react'
import les2 from "../../images/sport/trailOuVtt.png"

export default function SportSwitch() {


  return (
    
      <div className="sportContent">
        

      <div className="content" style={{flexDirection:"column"}}>
        <img src={les2} alt="saut en vlo" style={{marginBottom:"30px"}}/>
        <div className="sportText" style={{textAlign:"center"}}>
          <p>Dpasser ses limites / juste prendre du plaisir...</p><br />
          <p>Se rentrer dedans  s'en faire mal / regarder le paysage qui s'offre  nous...</p><br />
          <p>Se cloisonner dans sa bulle / partager un moment privilgi avec des gens habills en moulant, comme nous !</p><br />
          <p>La seule vrit est SA vrit au moment o on se la dicte !</p><br />
          <p>Et en plus, elle peut tre variable en fonction de l'humeur, de la mto, de l'envie...</p>
        </div>
      </div>
    </div>
  )
}
